body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
}

.navbar {
  background-color: #6a1b9a;
  padding: 1rem;
  display: flex;
  justify-content: space-around;
  gap: 1rem;
}

.navbar a {
  color: #ffffff;
  text-decoration: none;
  font-weight: bold;
}

.content nav {
  background-color: #8e24aa;
  padding: 0.5rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.content nav a {
  color: #ffffff;
  text-decoration: none;
  padding: 0.5rem;
  background-color: #7b1fa2;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.content nav a:hover {
  background-color: #6a1b9a;
}

.content {
  padding: 1rem;
}

h1, h2 {
  color: #6a1b9a;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}

td, th {
  padding: 1rem;
  text-align: left;
  vertical-align: middle;
}

button {
  background-color: #8e24aa;
  color: #ffffff;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  margin-right: 0.5rem;
}

button:hover {
  background-color: #6a1b9a;
}

input[type="text"] {
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: calc(100% - 1rem);
  margin: 0.5rem 0;
}

.add-button {
  margin-top: 1rem;
  display: inline-block;
  background-color: #8e24aa;
  color: #ffffff;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  border-radius: 50%;
  font-size: 1.5rem;
  line-height: 1rem;
  text-align: center;
  width: 40px;
  height: 40px;
}

.add-button:hover {
  background-color: #6a1b9a;
}
.styled-table {
  width: 100%;
  border-collapse: collapse;
}

.styled-table th, .styled-table td {
  padding: 12px;
  border: 1px solid #ddd;
  text-align: left;
}

.styled-table th {
  background-color: #6a1b9a;
  color: white;
}

.styled-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.add-button {
  margin-top: 10px;
  padding: 10px;
  background-color: #6a1b9a;
  color: white;
  border: none;
  cursor: pointer;
}

.add-button:hover {
  background-color: #7b1fa2;
}

textarea {
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  resize: vertical;
  min-height: 50px;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
  font-size: 14px;
}

textarea:focus {
  outline: none;
  border-color: #8e24aa;
}

div.text-content {
  white-space: pre-wrap;
  overflow-wrap: break-word;
}

.button-container {
  display: flex;
  gap: 5px;
}

.button-container button {
  flex: 1;
  white-space: nowrap;
}

.actions-column {
  width: 150px;
  text-align: center;
}
